/*Cases  css*/
.casescon1{
    width: 100%;
}
.casescon1>div{
    opacity: 0;
}
.casescon1>div:nth-child(1){
    opacity: 1;
}
.casescon1>div:nth-child(2){
    opacity: 1;
}
.casescon1>div:nth-child(3){
    opacity: 1;
}
.casescon1>div:nth-child(4){
    opacity: 1;
}
.casescon1>div:nth-child(5){
    opacity: 1;
}
.casescon1>div:nth-child(6){
    opacity: 1;
}
.casescon1>div:nth-child(7){
    opacity: 1;
}
.casescon1>div:nth-child(8){
    opacity: 1;
}
.casescon1-n{
    width: 25%;
    float: left;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    overflow: hidden;
    position: relative;
    cursor: pointer;
}
.casescon1-n img{
    width: 100%;
    -webkit-transition: 1.3s;
    -o-transition: 1.3s;
    transition: 1.3s;
    display: block;
}
.casescon1-n p{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.7);
    text-align: center;
    line-height: 337px;
    font-size: 24px;
    color: #fff;
    opacity: 0;
    -webkit-transition: .8s;
    -o-transition: .8s;
    transition: .8s;
}
.casescon1-n:hover p{
    opacity: 1;
}
.casescon1-n:hover img{
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}
.casescon2{
    width: 100%;
    padding-top: 63px;
    background: #F6F6F6;
}
.casescon2-title{
    text-align: center;
    font-weight: normal;
}
.casescon2x{
    margin: 0 auto;
    width: 100px;
    height: 1px;
    margin-top: 22px;
    margin-bottom: 34px;
    background: black;
}
.casescon2-title2{
    text-align: center;
    color: #7D7D7D;
}
.casescon2-con{
    width: 100%;
    padding-top: 75px;
}
.casescon2-con-n{
    width: 25%;
    float: left;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 150px;
    overflow: hidden;
    cursor: pointer;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    border: 1px solid #eee;
    border-left: none;
}
.casescon2-con-n:hover>div{
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg)
}
.casescon2-con-box{
    width: 100%;
    height: 100%;
    -webkit-transform-origin: center center;
        -ms-transform-origin: center center;
            transform-origin: center center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    position: relative;
}
.casescon2-top{
    width: 100%;
    height: 150px;
    background: #FF6600;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-top: 52px;
    position: absolute;
    line-height: 25px;
    color: white;
    -webkit-transform:  translateY(-76px)  rotateX(90deg);
            transform:  translateY(-76px)  rotateX(90deg);
}
.casescon2-bottom{
    width: 100%;
    height: 100%;
    text-align: center;
    padding-top: 25px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    line-height: 150px;
    -webkit-transform:  translateZ(75px);
            transform:  translateZ(75px);
    position: absolute;
}
.casescon2-bottom img{
    display:block;
    margin: 0 auto;
}


