/*contact css*/
.my-contact{
    height: 661px;
    position: relative;
    overflow: hidden;
    margin-top: -100px;
}
.my-contact .my-contact-us{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
}
.my-contact .my-contact-us img{
    -webkit-transform: translate(960px);
        -ms-transform: translate(960px);
            transform: translate(960px);
    -webkit-transform-origin: bottom left;
        -ms-transform-origin: bottom left;
            transform-origin: bottom left;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    -webkit-animation: d_img 1s forwards;
            animation: d_img 1s forwards;
}
@-webkit-keyframes  d_img{
    to{-webkit-transform: translate(150px);transform: translate(150px);}
}
@keyframes  d_img{
    to{-webkit-transform: translate(150px);transform: translate(150px);}
}
.my-contact .my-contact-us .my-contact-us-n{
    position: absolute;
    bottom: 100px;
    z-index: 9;
    width: 600px;
    text-align: right;
    color: #fff;
    -webkit-animation: d_n .8s forwards 1s;
            animation: d_n .8s forwards 1s;
    right: 0;
    opacity: 0;
}
@-webkit-keyframes  d_n{
    to{
        right: 80px;
        opacity: 1;
    }
}
@keyframes  d_n{
    to{
        right: 80px;
        opacity: 1;
    }
}
.my-contact .my-contact-us .my-contact-us-n h2{
    font-weight: normal;
    line-height: 0.9;
    font-size: 24px;
}
.my-contact .my-contact-us .my-contact-us-n h3{
    width: 100px;
    height: 1px;
    background: #f60;
    display: block;
    margin: 25px 0 25px auto;
}
.my-contact .my-contact-us .my-contact-us-n h4{
    font-weight: normal;
    line-height: 0.8;
    font-size: 18px;
}
.my-contact .my-contact-us .my-contact-us-n h5{
    margin-top: 50px;
    font-size: 14px;
    font-weight: normal;
}
.my-contact .my-contact-us .my-contact-us-n h5 span{
    color: #e4e4e4;
    display: block;
}
.my-contact .my-contact-us .my-contact-us-n h5 span:first-of-type+span{
    font-size: 12px;
    color: #c4c4c4;
}
.my-contact .my-contact-us .my-contact-us-n h5:after{
    content: "";
    display: block;
    width: 500px;
    height: 1px;
    background: #3b3b3f;
    float: right;
    margin-top: 20px;
}
.my-contact .my-contact-us .my-contact-us-n p{
    color: #e4e4e4;
    margin-top: 15px;
}
.my-contact-us img{
    position: absolute;
    -webkit-transform: translateX(150px);
        -ms-transform: translateX(150px);
            transform: translateX(150px);
    right: 0;
    bottom: 0;
    z-index: 8;
}
#allmap{
    height: 661px;
    overflow: hidden;
    position: relative;
    z-index: 0;
    background-color: rgb(243, 241, 236);
    color: rgb(0, 0, 0);
    text-align: left;
}
#allmap .anchorBL,#allmap .BMap_cpyCtrl{
    display: none;
}



